@use 'variables' as v;

h1, h2, h3 {
   font-family: v.$fuente;
   margin: 0 0 calc(v.$separador / 2);
   font-weight: v.$black;
   color: v.$blanco;
   text-align: center;
}

h1 {
   font-size: 4rem;
}

h2 {
   font-size: 3.6rem;
}

h3 {
   font-size: 3rem;
   text-align: center;
}

p {
   color: v.$blanco;
   font-size: 2rem;
}

.nombre-pagina {
   text-align: center;
   margin: 5rem 0;
   font-weight: v.$bold;

}

.descripcion-pagina {
   text-align: center;
}
