@use 'base/variables' as v;
@use 'base/mixins' as m;

.acciones {
   margin-top: 2rem;

   @include m.tablet {
      display: flex;
      justify-content: space-between;
      align-items: center;
   }

   a {
      color: v.$blanco;
      display: block;
      margin-bottom: calc(v.$separador / 2);
      text-align: center;
   }
}